<template>
    <!-- Root div -->
    <div @editing-item="editItem" class='row'>
        <!-- If isEditing = false, show a row item (data, descricao, valor, buttons) -->
        <div v-if="!isEditing" class='row-item'>
            <div>
                {{ data }}
            </div>
            <div>
                {{ descricao }}
            </div>
            <div>
                {{ valor }}
            </div>
            <div>
                <button @click="toggleOnEdit" class='row-button'>
                    <font-awesome-icon icon="pen-to-square" style="color: #33a"/>
                </button>
                <button @click="this.$emit('delete-item')" class='row-button'>
                    <font-awesome-icon icon="trash" style="color: #a33" />
                </button>
            </div>
        </div>
        
        <!-- Else, if isEditing = true, show an edit form -->
        <form v-else @submit.prevent='onSubmit' class='row-item'>
            <!-- First input: date -->
            <input type='date' v-model.lazy.trim="newDate"/>
            
            <!-- Second input: description (text) -->
            <input type='text' size=37 v-model.lazy.trim="newDesc"/>
            
            <!-- Third input: value (number) -->
            <input type='number' v-model.lazy.trim="newValue" />
            
            <!-- Buttons: confirm and cancel -->
            <div display='inline'>
                <button type="submit" @click="onSubmit" class='row-button'>
                    <font-awesome-icon icon="square-check" style="color: #3a3" />
                </button>
                <button @click="toggleOffEdit" class='row-button'>
                    <font-awesome-icon icon="right-from-bracket" style="color: #33a" />
                </button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        props: {
            data: { required: true, type: String },
            descricao: { required: true, type: String },
            valor: { required: true, type: Number }
        },
        
        data () {
            return {
                isEditing: false
            };
        },
        
        methods: {
            onSubmit () {
                const coisa = [this.newDate, this.newDesc, this.newValue];
                this.$emit("done-editing", coisa);
                this.isEditing = false;
            },
            toggleOffEdit () {
                this.$emit("cancel-edit");
                this.isEditing = false
            },
            toggleOnEdit () {
                this.$emit("edit-item");
                this.isEditing = true;
            },
        }
    }
</script>

<style scoped>
    .row {
        display: inline;
    }
    .row-item {
        display: flex;
        justify-content: space-between;
        margin: 0.5rem auto;
    }
    .row-button {
        padding: 0 0.5rem 0 0.5rem;
        margin: 0 0.5rem 0 0.5rem;
        border: 0pt;
    }
</style>
